import React, { Component } from 'react'
import { connect } from 'react-redux';
import Tooltip from '@material-ui/core/Tooltip';
import IconButton from '@material-ui/core/IconButton';
import Print from '@material-ui/icons/Print';
import ArrowBackSharp from '@material-ui/icons/ArrowBackSharp';
import { actionOpenSearch, actionOpenPrint } from '../../../redux/actions/searchTimeAction'
import MaterialVirtualizedTable from './MaterialVirtualizedTable'
import PagePdf from './PagePdf';
class DashboardReport extends Component {
    printOrder = () => {
        var content = document.getElementById('printme').innerHTML;
        var mywindow = window.open('', 'Print', 'height=500,width=800');

        mywindow.document.write('<html><head><title>IoT SYSTEM</title><link media="all" type="text/css" rel="stylesheet" href="./Report.css"/>');
        mywindow.document.write('</head><body >');
        mywindow.document.write(content);
        mywindow.document.write('</body></html>');

        mywindow.document.close();
        mywindow.focus()
        mywindow.print();
        mywindow.onfocus = function () {
            console.log('tat man hinh')
            setTimeout(function () { mywindow.close(); }, 1000); 
        }
    }
    render() {

        return (
            <div>
                <div>
                    <Tooltip title="OPEN SEARCH">
                        <IconButton onClick={this.props.actionOpenSearch} aria-label="Search">
                            <ArrowBackSharp />
                        </IconButton>
                    </Tooltip>
                    <Tooltip title="PRINT">
                        <IconButton onClick={() => this.printOrder()}>
                            <Print />
                        </IconButton>
                    </Tooltip>
                </div>
                <div>
                    {this.props.printed === false ?
                        <MaterialVirtualizedTable data={this.props.data} /> : null}
                        <PagePdf printableId='printme' data={this.props.data} />
                </div>
            </div>
        )
    }
}
const mapStateToProps = (state, ownProps) => {
    return {
        searched: state.searchTime.searched,
        printed: state.searchTime.printed,
    }
}
const mapDispatchToProps = (dispatch, ownProps) => {
    return {
        actionOpenSearch: () => {
            dispatch(actionOpenSearch())
        },
        actionOpenPrint: () => {
            dispatch(actionOpenPrint())
        }
    }
}
export default connect(mapStateToProps, mapDispatchToProps)(DashboardReport);